@import "sass/index";

.lines {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.linesBox {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-around;
  width: 90%;
  height: 30%;

  & :nth-child(1) {
    transform: rotate(45deg);
  }
  & :nth-child(2) {
    transform: rotate(40deg);
  }
  & :nth-child(3) {
    transform: rotate(35deg);
  }
  & :nth-child(4) {
    transform: rotate(25deg);
  }
  & :nth-child(5) {
    transform: rotate(15deg);
  }

  & :nth-child(6) {
    transform: rotate(0deg);
  }

  & :nth-child(7) {
    transform: rotate(-15deg);
  }
  & :nth-child(8) {
    transform: rotate(-25deg);
  }
  & :nth-child(9) {
    transform: rotate(-35deg);
  }
  & :nth-child(10) {
    transform: rotate(-40deg);
  }
  & :nth-child(11) {
    transform: rotate(-45deg);
  }
}
.line {
  width: 1px;
  height: 100%;
  opacity: 0.5;

  background: linear-gradient(0deg, #{use(--secondary-main)}, transparent);
}
